@import url(https://fonts.googleapis.com/css?family=Playfair+Display:400,700|Raleway:400,600);
body {
  margin: 0;
  padding: 0;
}

*{
  margin: 0;
  padding: 0;
}

h1, h2{
  font-family: 'Playfair Display', serif;
  font-weight: 500;
}

p, a, small{
  font-family: 'Raleway', sans-serif;
}

p{
  font-size: 20px;
}

h1{
  font-size: 48px;
}

h2{
  font-size: 36px;
  margin-bottom: 15px;
}

.intro{
  background-image: url(/portfolio/static/media/background-intro.f3cf96b9.jpg);
  -webkit-clip-path: polygon(0px 100%,100% 80%,100% 0px,0px 0px);
  -ms-clip-path: polygon(0px 100%,100% 80%,100% 0px,0px 0px);
  clip-path: polygon(0px 100%,100% 80%,100% 0px,0px 0px);
  background-position: center;
  height: 660px;
  background-size: cover;
  display: flex;
  flex-direction: column;
}

.intro-content{
  align-self: flex-end;
  padding: 180px;
}

.typewrite::after{
  content: "|";
  margin-left: 5px;
  opacity: 1;
  -webkit-animation: t-writer 1s infinite;
          animation: t-writer 1s infinite;
}

.btn{
  background-color: #fff;
  padding: 10px 20px;
  border: none;
  text-align: center;
  font-size: 14px;
  cursor: pointer;
  letter-spacing: 2px;
  border-bottom: 1px solid #fff;
  display: block;
  margin: 30px auto 0 auto;
}

.btn:hover{
  border-bottom: 1px solid #DED8C3;
  font-weight: 700;
}

.about{
  padding: 20px 197px;
}

.skills{
  padding: 140px 197px 80px 197px;
}

.portfolio, .contact{
  padding: 80px 197px;
}

.about p{
  width: 50%;
  line-height: 1.4;
}

.about-img{
  width: 200px;
  height: 200px;
  position: absolute;
  top: 10px;
  left: 10px;
  -webkit-filter: grayscale(10%);
          filter: grayscale(10%);
}

.about-img:hover{
  -webkit-filter: grayscale(100%);
          filter: grayscale(100%);
  cursor: crosshair;
}

.about-img-container{
  width: 200px;
  height: 200px;
  position: relative;
  background-color: #F5EFE3;
}

.skills-content, .works-options, .contact div, .about-content{
  display: flex;
  justify-content: space-around;
}

.skills{
  -webkit-clip-path: polygon(0px 100%,100% 100%,100% 0px,0px 20%);
  -ms-clip-path: polygon(0px 100%,100% 100%,100% 0px,0px 20%);
  clip-path: polygon(0px 100%,100% 100%,100% 0px,0px 20%);
  background-color: #F5EFE3;
  line-height: 1.6;
}

.sub-skill{
  margin-left: 20px;
}

.skills small{
  font-size: 16px;
  display: block;
}

.mockup{
  max-width: 45%;
  display: block;
  margin: 0 auto;
  position: absolute;
  z-index: 999;
  left: 27%;
}

.container{
  position: relative;
  display: block;
}

.image{
  width: 35%;
  top: 15px;
  left: 32%;
  position: absolute;
  z-index: -1;
}

.contact{
  background-color: #E6E7E5;
  text-align: center;
}

.contact a{
  display: block;
  color: #000;
  font-weight: 700;
  text-decoration: none;
  font-size: 14px;
  margin-top: 10px;
  text-transform: uppercase;
  letter-spacing: 2px;
}

.contact a:hover{
  color: #a6aa9c;
}

footer{
  background-color: #CCCCCC;
  text-align: center;
  padding: 5px 0;
  font-size: 12px;
}

footer a{
  color: #000;
  text-decoration: none;
  font-weight: 700;
}

@-webkit-keyframes t-writer {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
}

@keyframes t-writer {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
}

@media only screen and (min-device-width : 768px) and (max-device-width : 1024px) {
  .intro-content {
    padding: 100px 0;
  }
  .h1-intro {
    font-size: 44px;
  }
  .typewrite {
    font-size: 36px;
  }
  .mockup {
    left: 25%;
  }
  .image {
    left: 30%;
  }
}

@media only screen and (min-device-width : 0) and (max-device-width : 560px) {
  h1{
    font-size: 38px;
  }
  h2{
    font-size: 30px;
  }
  .typewrite{
    font-size: 28px !important;
  }
  #intro, #about, #portfolio, #contact {
    padding: 0;
  }
  .about p{
    padding: 0 60px;
  }
  .intro {
    background-image: url(/portfolio/static/media/background-mobile.4752ab61.jpg);
    -webkit-clip-path: polygon(0px 100%,100% 90%,100% 0px,0px 0px);
    -ms-clip-path: polygon(0px 100%,100% 90%,100% 0px,0px 0px);
    clip-path: polygon(0px 100%,100% 90%,100% 0px,0px 0px);
  }
  .intro-content {
    padding: 280px 0;
    align-self: center;
  }
  .h1-intro {
    font-size: 38px;
  }
  .typewrite {
    font-size: 30px;
  }
  #portfolio, #contact {
    padding: 40px 0;
  }
  #skills {
    padding: 100px 0;
    text-align: center;
    -webkit-clip-path: polygon(0px 100%,100% 100%,100% 0px,0px 10%);
    -ms-clip-path: polygon(0px 100%,100% 100%,100% 0px,0px 10%);
    clip-path: polygon(0px 100%,100% 100%,100% 0px,0px 10%);
  }
  nav {
    display: none;
  }
  .mockup {
    width: 80%;
  }
  .image {
    width: 35%;
    left: 32%;
    margin-top: -8px;
  }
  .works-options, #contact div, .skills-content, .about-content {
    display: flex;
    flex-direction: column;
  }
  #contact div {
    padding-top: 30px;
  }
  #contact p {
    margin-bottom: 30px;
  }
}
nav{
  padding: 30px 180px;
  align-self: flex-end;
  display: flex;
}

.nav-item{
  font-family: 'Raleway', sans-serif;
  text-transform: uppercase;
  background-color: transparent;
  padding: 10px 20px;
  border: none;
  text-align: center;
  font-size: 13px;
  cursor: pointer;
  letter-spacing: 2px;
  border-bottom: 1px solid transparent;
}
  
.nav-item:hover{
  color: #a6aa9c;
  font-weight: 700;
}
.works{
  margin-top: 40px;
  background-color: #fff;
  padding: 10px 20px;
  border: none;
  text-align: center;
  font-size: 14px;
  cursor: pointer;
  letter-spacing: 2px;
  border-bottom: 1px solid #fff;
  margin-top: 280px;
}
  
.works:hover{
  border-bottom: 1px solid #DED8C3;
  font-weight: 700;
}

@media only screen and (min-device-width : 0) and (max-device-width : 560px) {
  .works:first-child {
    margin-top: 130px !important;
  }
  .works {
    margin-top: 0 !important;
  }
}
.h1-content{
    padding-bottom: 40px;
    text-align: center;
  }
